import { Brands } from "./brands";
import { Masks } from "./masks";
import { Tokenize } from "./tokenize";

class Credit
{
    constructor() {
        new Masks;
        new Tokenize;
        new Brands;

        this.generateSession();
        this.get_customer_ip();
    }

    generateSession() {
        if (typeof getSessionID !== "undefined") {
            getSessionID(function (sessionId) {
                fetch(`${window.location.origin}/index.php/wp-json/wc-koin/checkout-fields`, {
                    method: 'POST',
                    headers: {
                      'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({
                        type: 'customer-session',
                        fields: {
                            session: sessionId,
                        }
                    })
                });
            });
        }
    }

    get_customer_ip() {
        fetch('https://api.ipify.org?format=json')
            .then(response => response.json())
            .then((data) => {
                if (data.ip) {
                    fetch(`${window.location.origin}/index.php/wp-json/wc-koin/checkout-fields`, {
                        method: 'POST',
                        headers: {
                          'Content-Type': 'application/json'
                        },
                        body: JSON.stringify({
                            type: 'ip-address',
                            fields: {
                              ip: data.ip,
                            }
                        })
                    });
                }
            });
    }
}

(function($) {
    $(document).on("ready updated_checkout", () => {
        new Credit;
    });
}(jQuery));
